import { Component, OnInit } from '@angular/core';
import { cloneDeep } from 'lodash-es';
import { FilteringFormRendererContext } from '@c8y/ngx-components';
import { DeviceGridColumn } from '../device-grid.models';

@Component({
  templateUrl: './group.filtering-form-renderer.component.html'
})
export class GroupFilteringFormRendererComponent implements OnInit {
  model: any;
  preselected: string[] = [];

  constructor(public context: FilteringFormRendererContext) {}

  ngOnInit() {
    const column: DeviceGridColumn = this.context.property;
    this.model = cloneDeep(column.externalFilterQuery || {});
    this.preselected = this.model.selectedNodes || [];
  }

  applyFilter() {
    this.context.applyFilter({
      externalFilterQuery: this.model
    });
  }

  resetFilter() {
    this.context.resetFilter();
  }

  selectionChanged(nodes) {
    this.model.selectedNodes = nodes;
  }
}
