import { Type } from '@angular/core';
import { ColumnDataType, SortOrder, FilterPredicateFunction, ColumnConfig } from '@c8y/ngx-components';
import {
  DeviceGridColumn,
  DeviceGridColumnFilteringConfig,
  DeviceGridColumnSortingConfig
} from '../device-grid.models';
import { BaseFilteringFormRendererComponent } from './base.filtering-form-renderer.component';

export class BaseDeviceGridColumn implements DeviceGridColumn {
  name: string;
  path?: string;
  header?: string;
  dataType?: ColumnDataType;

  visible?: boolean;
  positionFixed?: boolean;
  gridTrackSize?: string;

  headerCSSClassName?: string | string[];
  headerCellRendererComponent?: Type<any>;

  cellCSSClassName?: string | string[];
  cellRendererComponent?: Type<any>;

  sortable?: boolean;
  sortingConfig?: DeviceGridColumnSortingConfig;
  sortOrder?: SortOrder;

  filterable?: boolean;
  filteringConfig?: DeviceGridColumnFilteringConfig;
  filteringFormRendererComponent?: Type<any>;
  filterPredicate?: string | FilterPredicateFunction;
  externalFilterQuery?: string | object;

  constructor(config: ColumnConfig = {}) {
    this.filteringFormRendererComponent = BaseFilteringFormRendererComponent;
    Object.assign(this, config);
  }
}
