import { gettext, ColumnConfig } from '@c8y/ngx-components';
import { BaseDeviceGridColumn } from './base.device-grid-column';

export class TypeDeviceGridColumn extends BaseDeviceGridColumn {
  constructor(initialColumnConfig?: ColumnConfig) {
    super(initialColumnConfig);

    this.name = 'type';
    this.path = 'type';
    this.header = gettext('Type');

    this.filterable = true;
    this.filteringConfig = {
      schema: {
        type: 'object',
        properties: {
          types: {
            type: 'array',
            items: {
              type: 'string'
            }
          }
        }
      },
      form: [
        {
          key: 'types',
          title: gettext('Show devices with type'),
          add: gettext('Or'),
          items: [
            {
              key: 'types[]',
              notitle: true,
              placeholder: gettext('e.g. MyType, you can use an asterisk (*) as wildcard character')
            }
          ]
        }
      ],
      getFilter(model: any): any {
        const filter: any = {};
        if (model.types.length) {
          filter.type = { __in: model.types };
        }
        return filter;
      }
    };

    this.sortable = true;
    this.sortingConfig = { pathSortingConfigs: [{ path: 'type' }] };
  }
}
